.card {
  width: 90%;
  height: max-content;
  margin: 30px;
  display: flex;
  justify-content: space-between;
  padding: 15px;
  border-radius: 4px;
  background-color: #fff;
}

.cartegory-select {
  text-decoration: none;
  font-family: Montserrat, sans-serif;
  font-size: 0.813rem;
  font-weight: normal;
  letter-spacing: 1.9px;
  color: #121212;
}

.heading-one {
  font-family: RobotoSlab, monospace;
  font-size: 1.375rem;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: -0.2px;
  color: #121212;
}

.author-card {
  font-family: RobotoSlab, monospace;
  font-size: 0.875rem;
  font-weight: 300;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #4386bf;
}

.no-outline {
  border: none;
  font-family: RobotoSlab, monospace;
  font-size: 0.875rem;
  font-weight: 300;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #4386bf;
  background-color: #fff;
}

.button {
  display: flex;
  gap: 10px;
}

.book-details {
  width: 33.33%;
}
